html, body {
  height: 100%;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

h1 {
  text-align: center;
  margin-bottom: 0;
  margin-top: 0;
  font-family: 'Black Han Sans', sans-serif;
  width: auto;
  height: auto;
  padding: 0;
}

h2 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  font-family: 'Black Han Sans', sans-serif;
}

h3 {
  margin-bottom: 0;
}


img {
  border-radius: 50px;
}

ul {
  font-family: 'Black Han Sans', sans-serif;
}

div {
  /* word-spacing: ; */
  letter-spacing: .05em;
  line-height: 1.6rem;
  font-size: 1.1rem;
}

#app {
  position:relative;
}

.imgsituate {
  text-align: center;
}


.item {
  margin-top: 0;
  margin-bottom: 0;
  border-right: 1px solid gray;
  background-color: #eff3f7;
}

#grid {
  display: grid;
  grid-template-columns: 150px 1fr;
}

.BlogContent {
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 20px;
}

.BlogContent h2:first-child {
  margin-bottom: 30px;
}

.SetColor {
  color: #808080;
}

.CursorPointer {
  cursor: pointer;
}

.container {
  margin-top: 100px;
  max-width: 1280px;
  min-height: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}
